// 
import Router from '@/router'
import { reLogin } from '@/api/user'
import { Toast } from 'vant'
import axios from 'axios'
import store from '@/store'
const request = axios.create({
  // 
  baseURL: 'http://geek.itheima.net'
})
// 
request.interceptors.request.use(
  (config) => {
    // 
    const { user } = store.state
    if (user && user.token && config.headers.Authorization === undefined) {
      config.headers.Authorization = `Bearer ${user.token}`
    }
    // config
    return config
  },
  (err) => {
    return Promise.reject(err)
  }
)
// 
request.interceptors.response.use(
  (response) => {
    // 2xx 3xx
    // 
    return response
  },
  async (err) => {
    //  2xx (4xx 4xx)
    // 

    // 401
    // thisthis
    if (err.response.status === 401) {
      if (!store.state.user) {
        // login
        Router.replace(`/login?path=${Router.currentRoute.fullPath}`)
        Toast({ type: 'warning', message: '' })
      } else {
        // 
        // store.commit('setUser', null)
        // // Notify({ type: 'warning', message: '' })
        // // setTimeout(() => {
        // Router.replace('/login')

        // refreshtoken
        const { data: res } = await reLogin()
        // console.log(res.data.token)
        store.commit('setUser', res.data.token)
        // token
        err.config.headers.Authorization = `Bearer ${res.data.token}`
        // err.configreturn
        // console.log(err)
        return axios(err.config)
      }
    } else if (err.response.status === 500 && err.config.url === '/v1_0/authorizations' && err.config.method === 'put') {
      store.commit('setUser', null)
      Router.replace(`/login?path=${Router.currentRoute.fullPath}`)
      Toast({ type: 'warning', message: '' })
    } else {
      Toast('')
    }
    return Promise.reject(err)
  }
)
export default request
